* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", "Noto Sans KR";
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

fieldset {
  border: none;
}

legend {
  font-size: 0;
}

caption {
  font-size: 0;
}

body.isScroll {
  overflow: hidden;
}

header {
  position: relative;
  width: 100%;
  background: #f6f8f6;
  z-index: 1;
}
header.sub .inner {
  padding: 20px;
}
header .inner {
  display: flex;
  width: 1328px;
  margin: 0 auto;
  padding: 40px 0;
  align-items: center;
}
header .logo {
  width: 31%;
  font-size: 37px;
}
header .logo span {
  color: #4a6869;
}
header .gnb {
  position: relative;
  width: 46%;
  display: flex;
  justify-content: space-between;
}
header .gnb .line {
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100px;
  height: 0px;
  background: #4a6869;
  transition: all 0.5s;
  transition-timing-function: cubic-bezier(ease-in-out);
}
header .gnb .line.on {
  height: 5px;
}
header .gnb li {
  position: relative;
  font-size: 19px;
  color: #4a6869;
  font-weight: 500;
  overflow: hidden;
  transition: all 0.5s;
}
header .gnb li a {
  transition: all 0.5s;
}
header .gnb li a:hover {
  color: #2bae29;
}
header .join {
  position: relative;
  display: flex;
  justify-content: center;
  width: 13%;
  overflow: hidden;
  margin-left: auto;
  padding: 16px 42px;
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  border: solid 1px #e0e7e1;
  border-radius: 100px;
  color: #4a6869;
  transition: all 0.5s;
}
header .join span {
  opacity: 0;
  color: inherit;
  transform: translate3d(0, -10px, 0);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
header .join::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 14px 0;
  color: inherit;
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
header .join:hover {
  border: solid 1px #4a6869;
}
header .join:hover::before {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
header .join:hover span {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
header .join:hover span:nth-child(1) {
  transition-delay: 0.045s;
}
header .join:hover span:nth-child(2) {
  transition-delay: 0.105s;
}
header .join:hover span:nth-child(3) {
  transition-delay: 0.165s;
}
header .join:hover span:nth-child(4) {
  transition-delay: 0.225s;
}
header .join:hover span:nth-child(5) {
  transition-delay: 0.285s;
}
header .totalMenuBtn {
  display: none;
  border: none;
  position: relative;
  width: 35px;
  height: 25px;
  border-bottom: solid 2px #000;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.5s;
}
header .totalMenuBtn::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  width: 100%;
  height: 2px;
  background: #000;
  transform-origin: left;
  transition: all 0.5s;
}
header .totalMenuBtn::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  width: 100%;
  height: 2px;
  background: #000;
  transform-origin: left;
  transition: all 0.5s;
}
header .totalMenuBtn.on {
  width: 30px;
  height: 30px;
  border: none;
}
header .totalMenuBtn.on::before {
  width: 140%;
  transform: rotateZ(45deg);
  background: #fff;
}
header .totalMenuBtn.on::after {
  width: 140%;
  top: calc(100% - 1px);
  transform: rotateZ(-45deg);
  background: #fff;
}

@media screen and (max-width: 1439px) {
  header .inner {
    width: 100%;
    padding: 40px 20px;
  }
  header .logo {
    width: 20%;
    margin-left: 40px;
    font-size: 28px;
  }
  header .gnb {
    width: 58%;
  }
  header .join {
    width: 10%;
    margin-right: 20px;
    padding: 11px 0;
    font-size: 18px;
  }
}
