    justify-content: space-between;
    padding: 20px;
  }
  header .logo {
    width: initial;
    margin-left: 0;
  }
  header .gnb {
    display: none;
  }
  header .join {
    display: none;
  }
  header .totalMenuBtn {
    margin-right: 0px;
    display: block;
  }

  header.mobile {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
