  }
  header.mobile .join {
    display: flex;
    justify-content: center;
    width: initial;
    overflow: hidden;
    margin: 26px 0 0 0;
    padding: 9px 20px;
    text-align: center;
    font-size: 22px;
    font-weight: 500;
    border: solid 1px #e0e7e1;
    border-radius: 100px;
    color: #c2d9d9;
    transition: all 0.5s;
    align-items: center;
  }
  header.mobile .join span {
    opacity: 0;
    color: inherit;
    transform: translate3d(0, -10px, 0);
    transition: transform 0.3s, opacity 0.3s;
    transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
  }
  header.mobile .join::before {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 9px 0;
    color: inherit;
    transition: transform 0.3s, opacity 0.3s;
    transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
  }
  header.mobile .join:hover {
    color: #bee78e;
    border: solid 1px #bee78e;
  }
  header.mobile .join:hover::before {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  header.mobile .join:hover span {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  header.mobile .join:hover span:nth-child(1) {
    transition-delay: 0.045s;
  }
  header.mobile .join:hover span:nth-child(2) {
    transition-delay: 0.105s;
  }
  header.mobile .join:hover span:nth-child(3) {
    transition-delay: 0.165s;
  }
  header.mobile .join:hover span:nth-child(4) {
    transition-delay: 0.225s;
  }
  header.mobile .join:hover span:nth-child(5) {
    transition-delay: 0.285s;
  }
}
