.loadingWrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.9);
  z-index: -1;
  opacity: 0;
  transition: all 1s;
}
.loadingWrap .loading {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: inline-block;
  border: 3px solid;
  border-color: #fff #fff transparent transparent;
  animation: loadingRotation 1s linear infinite;
}
.loadingWrap .loading::before, .loadingWrap .loading::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border: 3px solid;
  border-color: transparent transparent #ff3d00 #ff3d00;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  animation: loadingRotationBack 0.5s linear infinite;
  transform-origin: center center;
}
.loadingWrap .loading::before {
  width: 32px;
  height: 32px;
  border-color: #fff #fff transparent transparent;
  animation: loadingRotation 1.5s linear infinite;
}
.loadingWrap.on {
  z-index: 10;
  opacity: 1;
}

@keyframes loadingRotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loadingRotationBack {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

